import { Box, TextField, Grid, InputAdornment, Button } from '@mui/material';
import React, { useState } from 'react';
import { ToastContainer } from 'react-toastify';
import CurrencyRupeeIcon from '@mui/icons-material/CurrencyRupee';
import { movePayInIntoPayout } from '../../../api/login/Login';
import Loader from '../../../common/loader/Loader';
import toastMessage from '../../../utils/toastMessage';
import { useNavigate } from 'react-router-dom';


// Reusable CustomTextField
const CustomTextField = (props) => (
  <TextField
    variant="outlined"
    size="small"
    InputLabelProps={{ shrink: true }}
    sx={{
      "& label.Mui-focused": { color: "var(--bs-nav)" },
      "& .MuiOutlinedInput-root": {
        height: 40,
        "& .MuiOutlinedInput-input": { py: 0 },
        "&:hover fieldset": { borderColor: "var(--bs-primary1)" },
        "&.Mui-focused fieldset": { borderColor: "var(--bs-primary1)" },
      },
    }}
    {...props}
  />
);

// Reusable CustomBox
const CustomBox = ({ children }) => (
  <Box
    sx={{
      width: "100%",
      mx: "auto",
      my: 1,
      py: 3,
      px: 3,
      bgcolor: "white",
      borderRadius: 1,
      boxShadow: 0,
    }}
  >
    {children}
  </Box>
);

// Main Component
const TransferPayInTOPayout = ({ walletData, walletCall }) => {
  const navigate =   useNavigate()
  const [values, setValues] = useState({
    amount: "",
    remark: ""
  });

  const [loader , setLoader] = useState(false)

  const [errors, setErrors] = useState({});

  const handleChange = (field) => (e) => {
    const value = e.target.value;
    setValues((prev) => ({ ...prev, [field]: value }));
    if (errors[field]) setErrors((prev) => ({ ...prev, [field]: "" }));
  };

  const validateStep1 = () => {
    const next = {};
    const amt = Number(values.amount);

    if (!values.amount) next.amount = "Enter an amount";
    else if (Number.isNaN(amt) || amt <= 99) next.amount = "Minimum Amount is 100.";
    else if (amt > (walletData?.payin_wallet || 0)) next.amount = "Amount exceeds wallet balance";

    if (!values.remark.trim()) next.remark = "Enter remark";

    setErrors(next);
    return Object.keys(next).length === 0;
  };

  const handleNext = async() => {
    if (validateStep1()) {
      console.log("Submit Payload:", values);
       setLoader(true)
     
       try {
          let res  = await movePayInIntoPayout(values)
          toastMessage(res?.data?.error , res?.data?.message)
            if(res?.data?.statusCode === '200')
            {
                setTimeout(()=>{
                    walletCall()
                    navigate('/payin-wallet-to-payout-wallet-report')
                },500)
               
            }
        
       } catch (error) {
        
       }finally {
        setLoader(false)
       }
      // Perform submit logic here
    }
  };

  return (
    <>
    
      {loader && <Loader/>}
      <div className="PageHeading">
        <h1 style={{ textTransform: 'none' }}>
          Transfer PayIn Wallet to Main Wallet
        </h1>
      </div>

      <CustomBox>
        <Grid container spacing={2} >
          <Grid item xs={12} md={6} lg={4}>
            <CustomTextField
              label="Amount"
              name="amount"
              type="number"
              placeholder="Enter Amount"
              value={values.amount}
              onChange={handleChange("amount")}
              error={Boolean(errors.amount)}
              helperText={errors.amount}
              autoComplete="off"
              inputProps={{ inputMode: 'numeric', maxLength: 10 }}
              InputProps={{
                startAdornment: (
                  <InputAdornment position="start">
                    <CurrencyRupeeIcon />
                  </InputAdornment>
                ),
              }}
              fullWidth
            />
          </Grid>

          <Grid item xs={12} md={6} lg={4} >
            <CustomTextField
              label="Remark"
              name="remark"
              type="text"
              value={values.remark}
              onChange={handleChange("remark")}
              error={Boolean(errors.remark)}
              helperText={errors.remark}
              autoComplete="off"
             
              fullWidth
            />
          </Grid>

          <Grid item xs={12} sx={{marginTop:2}}>
            <Button
              onClick={handleNext}
              color="primary"
              variant="contained"
              sx={{
                backgroundColor: 'var(--bs-primary1)',
                borderRadius: 2,
                px: 4,
                width: '150px',
              }}
            >
              Submit
            </Button>
          </Grid>
        </Grid>
      </CustomBox>
    </>
  );
};

export default TransferPayInTOPayout;
